import React from 'react';

import './Login.scss';

export function Login(props) {

  const { history } = props

  const loginBtn = (str, event)=>{
    history.push('/home')
  }


  return (
    <div className="container-login">

      <div className="login-img f_c_c">
        <img src={require('../assets/img/logo.png')} alt="" />
      </div>

      <form className="form-box">
        <div className="input-ctrl f_l">
          <input type="tel" maxLength={11} placeholder="" />
          <button disabled={ true } className="code-btn"></button>
        </div>
        <div className="input-ctrl f_l">
          <input type="tel" maxLength={8} placeholder="" />
        </div>
      </form>

      <p className="login-xy">
        
        <a href="/">  </a>
        
        <a href="/">  </a>
      </p>

      <button className="login-btn" onClick={ loginBtn.bind(null, '1')  }>
        
      </button>

      <p className="login-about"></p>

    </div>
  )
}